function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        slidesPerView: 5,
        spaceBetween: 15,
        freeMode: true,
        watchSlidesProgress: true,
        direction: "horizontal",
        breakpoints: {
            320: {
                slidesPerView: 3,
            },
            400: {
                slidesPerView: 4,
            },
            575: {
                slidesPerView: 5,
            },
        }
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 10,
        slidesPerView: 1,
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
        },
    });
}


$(document).ready(function() {
    sliderMain();
    sliderNews();
    sliderAbout();
    sliderTours();
    sliderTours2();
    sliderSeaCrimea();
    sliderSeaKrasnodar();
    sliderSeaAbkhazia();
    sliderCatalog();
    gallery();

    // Hover-Click
    $(".header-bottom__item").hover(onIn, onOut);
    $(".header-bottom__item .header-bottom__item-show").click(function() {
        if ($(this).parents(".header-bottom__item").hasClass("header-bottom__item--active")) {
            $(this).parents(".header-bottom__item").removeClass("header-bottom__item--active")
        } else {
            $(this).parents(".header-bottom__item").addClass("header-bottom__item--active")
        }
    })

    // Burger
    $('.header-top__burger').click(function () {
        $(this).toggleClass('header-top__burger--active')
        $('.header-bottom__inner').toggleClass('header-bottom__inner--active')
        $('body').toggleClass('fixed-body')
    })

    // SidebarBurger
    $('.sidebar__burger').click(function () {
        $(this).toggleClass('sidebar__burger--active')
        $('.sidebar__menu').toggleClass('sidebar__menu--active')
        $('body').toggleClass('fixed-body')
    })

    // Accordion
    $(".sidebar__item-show").click((function(){
    $(this).toggleClass("sidebar__item-show--active"),
    $(this).siblings(".sidebar__item-hidden").slideToggle()}));

    // Filter
    $('.catalog__filter').click(function () {
        $(this).addClass('catalog__filter--active')
        $('.catalog__left-wrapp').addClass('catalog__left-wrapp--active')
        $('.header').css('z-index','1')
        $('body').addClass('fixed-body')
    })

    $('.catalog__left-close').click(function () {
        $('.catalog__filter').removeClass('catalog__filter--active')
        $('.catalog__left-wrapp').removeClass('catalog__left-wrapp--active')
        $('.header').css('z-index','2')
        $('body').removeClass('fixed-body')
    })

    // Tabs
    $(".tours__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tours__tab").removeClass("tours__tab--active")
        $(this).addClass("tours__tab--active")

        $(this).parents(".tours__inner").find(".tours__content").removeClass("tours__content--active")
        $(this).parents(".tours__inner").find(`.tours__content[data-content-path="${path}"]`).addClass("tours__content--active")
    })

    // Tabs2
    $(".info__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".info__tab").removeClass("info__tab--active")
        $(this).addClass("info__tab--active")

        $(this).parents(".info__inner").find(".info__content").removeClass("info__content--active")
        $(this).parents(".info__inner").find(`.info__content[data-content-path="${path}"]`).addClass("info__content--active")
    })



    // Range
    const rangeInput = document.querySelectorAll('.range-input input')
    priceInput = document.querySelectorAll('.price-input input')
    progress = document.querySelector('.slider .progress')

    let priceGap = 10000

    priceInput.forEach(input =>{
        input.addEventListener("input", e => {

            let minVal = parseInt(priceInput[0].value),
                maxVal = parseInt(priceInput[1].value)

            if ((maxVal - minVal >= priceGap) && maxVal <= 250000) {
                if (e.target.className === 'input-min') {
                    rangeInput[0].value = minVal;
                    progress.style.left = (minVal / rangeInput[0].max) * 100 + '%'
                } else {
                    rangeInput[1].value = maxVal;
                    progress.style.right = 100 - (maxVal / rangeInput[1].max) * 100 + '%'
                }
            }
        })
    })

    rangeInput.forEach(input =>{
        input.addEventListener("input", e => {

            let minVal = parseInt(rangeInput[0].value),
                maxVal = parseInt(rangeInput[1].value)

            if (maxVal - minVal < priceGap) {
                if (e.target.className === 'range-min') {
                    rangeInput[0].value = maxVal - priceGap;
                } else {
                    rangeInput[1].value = minVal + priceGap
                }
            } else {
                priceInput[0].value = minVal;
                priceInput[1].value = maxVal;
                progress.style.left = (minVal / rangeInput[0].max) * 100 + '%'
                progress.style.right = 100 - (maxVal / rangeInput[1].max) * 100 + '%'
            }
        })
    })
    $('.input-min').add()

    // Up
    var btn = $('.up');
    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });
    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

    // Btnt-green
    $('.btn-green').mouseenter(function (e) {
        x = e.pageX - $(this).offset().left;
        y = e.pageY - $(this).offset().top;
        $(this).find('.btn-green__animation').css({top:y, left:x})
    })
    $('.btn-green').mouseout(function (e) {
        x = e.pageX - $(this).offset().left;
        y = e.pageY - $(this).offset().top;
        $(this).find('.btn-green__animation').css({top:y, left:x})
    })

    // Btnt-red
    $('.btn-red').mouseenter(function (e) {
        x = e.pageX - $(this).offset().left;
        y = e.pageY - $(this).offset().top;
        $(this).find('.btn-red__animation').css({top:y, left:x})
    })
    $('.btn-red').mouseout(function (e) {
        x = e.pageX - $(this).offset().left;
        y = e.pageY - $(this).offset().top;
        $(this).find('.btn-red__animation').css({top:y, left:x})
    })

    // Btnt-blue
    $('.btn-blue').mouseenter(function (e) {
        x = e.pageX - $(this).offset().left;
        y = e.pageY - $(this).offset().top;
        $(this).find('.btn-blue__animation').css({top:y, left:x})
    })
    $('.btn-blue').mouseout(function (e) {
        x = e.pageX - $(this).offset().left;
        y = e.pageY - $(this).offset().top;
        $(this).find('.btn-blue__animation').css({top:y, left:x})
    })

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

    // Animation
    const animItems = document.querySelectorAll('._anim-items')

    if (animItems.length > 0) {
        window.addEventListener('scroll', animOnScroll)
        function animOnScroll(params) {
            for (let index = 0; index < animItems.length; index++) {
                const animItem = animItems[index];
                const animItemHeight = animItem.offsetHeight;
                const animItemOffset = offset(animItem).top;
                const animStart = 4;

                let animItemPoint = window.innerHeight - animItemHeight / animStart;

                if (animItemHeight > window.innerHeight) {
                    animItemPoint = window.innerHeight - window.innerHeight / animStart;
                }

                if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                    animItem.classList.add('active');
                } else {
                    if (!animItem.classList.contains('anim-no-hide')) {
                        animItem.classList.remove('active')
                    }
                }
            }
        }
        function offset(el) {
            const rect = el.getBoundingClientRect(),
                scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
                scrollTop = window.pageYOffset || document.documentElement.scrollTop;
            return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
        }

        setTimeout(() => {
            animOnScroll();
        }, 300)
    }

})



function onIn() {
    if (window.innerWidth > 991) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-bottom__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 991) {
        $(this).removeClass("header-bottom__item--active")
    }
}